/* global */
@font-face {
  font-family: "Montserrat";
  src: url("../../fonts/Montserrat-Regular.woff2") format("woff2"), url("../../fonts/Montserrat-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../../fonts/Montserrat-Medium.woff2") format("woff2"), url("../../fonts/Montserrat-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../../fonts/Montserrat-Bold.woff2") format("woff2"), url("../../fonts/Montserrat-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../../fonts/Montserrat-SemiBold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap;
}
html {
  height: 100%;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.43;
  color: #333333;
  margin: 0;
  background-color: #ffffff;
  text-align: center;
}
.button {
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  cursor: pointer;
  border: none;
  background-color: #7859cf;
  padding: 16px;
  color: #ffffff;
  border-radius: 4px;
}
.button:hover,
.button:focus {
  background-color: #9070ec;
}
.button:active {
  background-color: #593cac;
}
.button:disabled {
  background-color: #bdbdbd;
}
.link {
  cursor: pointer;
  text-decoration: none;
  color: #333333;
}
.subtitle {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.33;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #7859cf;
  margin: 0;
}
.nojs .navigation {
  display: block;
  position: static;
  order: 3;
}
@media (min-width: 768px) {
  .nojs .navigation {
    order: 0;
  }
}
.nojs .header__wrapper {
  flex-direction: column;
}
@media (min-width: 768px) {
  .nojs .header__wrapper {
    flex-direction: row;
  }
}
.nojs .header__toggle-wrapper {
  display: none;
}
.nojs .header__logo {
  margin: 0 auto;
}
.nojs .header__list {
  padding-bottom: 10px;
}
.nojs .address__map {
  background: #f8f8f8 url("../img/map_mobile.jpg") no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .nojs .address__map {
    background: #f8f8f8 url("../img/map_mobile@2.jpg") no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) {
  .nojs .address__map {
    background: #f8f8f8 url("../img/map-tablet.jpg") no-repeat;
    background-size: cover;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .nojs .address__map {
    background: #f8f8f8 url("../img/map-tablet@2.jpg") no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1440px) {
  .nojs .address__map {
    background: #f8f8f8 url("../../img/map-desktop.jpg") no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .nojs .address__map {
    background: #f8f8f8 url("../../img/map-desktop@2.jpg") no-repeat;
    background-size: cover;
  }
}
/* blocks*/
@media (min-width: 1440px) {
  .page__body {
    background-color: #e2e2e2;
  }
}
