  .page__wrapper {
    max-width: 1440px;
    margin: 0 auto;
    box-shadow: -15px 0 15px rgba(0, 1, 1, 0.1), 15px 0 15px rgba(0, 1, 1, 0.1);
  }
}
.header {
  background-color: #ffffff;
  padding: 0 12px 0 20px;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding-right: 20px;
    padding-bottom: 0;
    padding-top: 6px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding-left: 40px;
    padding-right: 38px;
    padding-top: 3px;
  }
}
.header__wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media (min-width: 768px) {
  .header__wrapper {
    justify-content: center;
  }
}
.header__nav {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 2;
}
@media (min-width: 768px) {
  .header__nav {
    display: flex;
    position: static;
    z-index: 0;
  }
}
.header--opened .header__nav {
  display: flex;
}
.header__toggle-wrapper {
  padding: 10px;
}
@media (min-width: 768px) {
  .header__toggle-wrapper {
    display: none;
  }
}
.header__toggle {
  width: 16px;
  height: 13px;
  border: none;
  background-color: #333333;
  display: flex;
  align-items: center;
  position: relative;
  margin-left: 12px;
  cursor: pointer;
}
.header__toggle::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 3.5px;
  background-color: #ffffff;
  top: 2px;
  left: 0;
}
.header__toggle::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 3.5px;
  background-color: #ffffff;
  bottom: 2px;
  left: 0;
}
.header__logo {
  margin-right: auto;
}
.header__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .header__list {
    margin-left: auto;
  }
}
.header--opened .header__toggle::after {
  background-color: #333333;
  transform: rotate(-45deg);
  bottom: 6px;
}
.header--opened .header__toggle::before {
  transform: rotate(45deg);
  background-color: #333333;
  top: 3px;
}
.header--opened .header__toggle {
  background-color: #ffffff;
}
.logo {
  display: flex;
}
.logo__text {
  display: none;
}
@media (min-width: 1440px) {
