    justify-content: flex-end;
    margin-right: -20px;
  }
}
.pagination__item {
  cursor: pointer;
  text-decoration: none;
  color: #333333;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  color: #7859cf;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  flex: 0 0 40px;
}
@media (min-width: 768px) {
  .pagination__item {
    width: 40px;
    padding: 10px;
    padding-left: 10px;
    margin-right: 0;
    border-radius: 0;
  }
  .pagination__item--text {
    flex: 0 0 150px;
    padding: 10px;
    border-radius: 4px 0 0 4px;
    justify-content: end;
    padding-right: 44px;
  }
  .pagination__item--next {
    padding-left: 7px;
    border-radius: 0;
  }
}
.pagination__item--current {
  background-color: inherit;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #333333;
}
@media (min-width: 768px) {
  .pagination__item--current {
    margin-left: 20px;
    margin-right: 20px;
  }
}
.pagination__item:hover,
.pagination__item:focus {
  color: #ffffff;
  background-color: #9070ec;
}
.pagination__item:active {
  background-color: #593cac;
  color: #ffffff;
}
.pagination__item--disabled {
  background-color: #e2e2e2;
  color: #ffffff;
}
.pagination__item:last-child {
  margin-right: 0;
}
.pagination__img {
  fill: #7859cf;
}
@media (min-width: 768px) {
  .pagination__img {
    display: none;
  }
}
.pagination__item--next .pagination__img {
  transform: rotate(180deg);
}
.pagination__text {
  display: none;
}
@media (min-width: 768px) {
  .pagination__text {
    display: block;
    font-size: 14px;
    line-height: 1.71;
    color: #7859cf;
  }
}
.address {
  background-color: #f8f8f8;
  background-size: contain;
  background-repeat: no-repeat;
  height: auto;
}
.address__map {
  display: block;
  border: none;
  width: 100%;
  min-height: 420px;
}
@media (min-width: 768px) {
  .address__map {
    min-height: 480px;
  }
}
@media (min-width: 1440px) {
  .address__map {
    min-height: 540px;
  }
}
